import React from 'react'
import { Link } from 'react-router-dom'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap/dist/js/bootstrap.min.js'

const Navbar = () => {
  return (
    <div>
      <nav class="navbar navbar-expand-lg navbar-light">
  <div class="container-fluid">
    <a class="navbar-brand" href="#">Navbar</a>
    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
      <div class="navbar-nav">
        <a class="nav-link active" aria-current="page" href="#"> <Link to={'/'}>Home</Link></a>
        <a class="nav-link" href="#"> <Link style={{textDecoration:'none',color:''}} to={'/Projects'}>Projects</Link></a>
        <a class="nav-link" href="#"> <Link style={{textDecoration:'none',color:''}} to={'/Qualification'}>Qualification</Link></a>
        <a class="nav-link" href="#">  <Link to={'/Contact'}>Contact</Link></a>
        <a class="nav-link disabled" href="#" tabindex="-1" aria-disabled="true"><Link to={'/About'}>About</Link></a>
      </div>
    </div>
  </div>
</nav>
    </div>
  )
}

export default Navbar